import { Injectable } from '@angular/core';
import { Reminder, Forecast } from '../model/reminder.model';
import { ID, EntityStore, StoreConfig, EntityState } from '@datorama/akita';
import * as moment from 'moment';

export interface ReminderState extends EntityState<Reminder, string> {
  ui: {
    selected: Partial<Reminder>,
    current_date: string,
    current_month: string,
    reminders_month: Object,
    day_viewer_opened: boolean,
    reminder_editor_opened: boolean,
    forecast?: Forecast,
  }
}

export function createInitialState(): ReminderState {
  return {
    ui: {
      selected: {} as Partial<Reminder>,
      current_date: moment().format('YYYY-MM-DD'),
      current_month: moment().format('YYYY-MM'),
      reminders_month: {},
      day_viewer_opened: false,
      reminder_editor_opened: false,
      forecast: null,
    }
  }
}

@Injectable({providedIn: 'root'})
@StoreConfig({name: 'reminders'})
export class ReminderStore extends EntityStore<ReminderState> {
  
  constructor() {
    super(createInitialState());
  }

  /**
   * Changes the selected reminder in the ui.
   * @param selected 
   */
  setSelected(selected: Reminder) {
    this.updateUi({selected});
  }

  /**
   * Clears the selected reminder in the ui.
   */
  clearSelected() {
    this.updateUi({selected: {}});
  }

  /**
   * Changes the value of the current date in the ui.
   * @param currentDate 
   */
  setCurrentDate(currentDate: string) {
    this.updateUi({current_date: currentDate});
  }

  /**
   * Changes the value of the current month in the ui.
   * @param currentMonth 
   */
  setCurrentMonth(currentMonth: string) {
    this.updateUi({current_month: currentMonth});
  }

  /**
   * Changes the value of day_viewer_opened.
   * @param dayViewerOpened 
   */
  setDayViewerOpened(dayViewerOpened: boolean) {
    this.updateUi({day_viewer_opened: dayViewerOpened});
  }

  /**
   * Changes the value of reminder_editor_opened
   * @param reminderEditorOpened 
   */
  setReminderEditorOpened(reminderEditorOpened: boolean) {
    this.updateUi({reminder_editor_opened: reminderEditorOpened});
  }

  /**
   * Updates the ui with the propertyToOverride field.
   * @param propertyToOverride 
   */
  private updateUi(propertyToOverride) {
    const updatedUi = {
      ...this.getValue().ui,
      ...propertyToOverride,
    }
    
    this.update({ui: updatedUi});
  }

  /**
   * Saves the cities in the store.
   */
  setCities(cities) {
    this.updateUi({cities});
  }

  /**
   * Updates the reminders_month with all the reminders for that month grupped 
   *    by date.
   */
  updateRemindersForCurrentMonth() {
    const currentMonth = this.getValue().ui.current_month;
    const reminders = Object.entries(this.getValue().entities);
    const remindersInMonth = reminders.filter(([id, reminder]) => (
      this.isReminderInCurrentMonth(reminder.date, currentMonth)));
    const remindersClean = reminders.map(([id, reminder]) => reminder);
    const groupedReminders = this.groupByKey(remindersClean, 'date');

    this.updateUi({reminders_month: groupedReminders});
  }

    /**
   * Returns tru if the date is in the currentMonth.
   * @param date 
   * @param currentMonth 
   */
  isReminderInCurrentMonth(date: string, currentMonth: string) {
    const dateWithoutDay = moment(date, 'YYYY-MM-DD').format('YYYY-MM');
    return dateWithoutDay === currentMonth;
  }

  /**
   * Groups the given entries in an object using the given key as id.
   * @param entries 
   * @param key 
   */
  groupByKey(entries, key) {
    return entries.reduce((grouped, entry) => {
      const groupedEntries = grouped[entry[key]] || [];
      
      groupedEntries.push(entry);
      grouped[entry[key]] = groupedEntries;

      return grouped;
    }, {});
  }

  /**
   * Sets a new value to the forecast.
   * @param forecast 
   */
  setForecast(forecast: Forecast) {
    this.updateUi({forecast});
  }
}