import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { City, Forecast } from "../model/reminder.model";
import { ReminderQuery } from "../store/reminder.query";
import * as moment from 'moment';
import { tap } from 'rxjs/operators';
import { ReminderStore } from "../store/reminder.store";

@Injectable()
export class WeatherService {
  
  /**
   * Base URL of the weather service.
   */
  private baseUrl = 'https://api.openweathermap.org/data/2.5/onecall';

  /**
   * API key used in every request.
   */
  private key = 'aeece9a48c100e57e889d1d8381871e3';
  
  constructor(
    private httpService: HttpClient,
    private reminderQuery: ReminderQuery,
    private store: ReminderStore,
  ) {}
  
  /**
   * Returns the weather for the given city, in the given date.
   * @param city 
   * @param date 
   */
  getWeatherForCity(city: City, date: string) {
    this.isDateBetweenAllowedRange(date) ? 
        this.processWeatherCall(city, date) :
        this.setDataNoAvailable();
  }

  /**
   * Sets the current forecast as data not available and loaded false, to try
   *    to load it later.
   */
  setDataNoAvailable() {
    const forecast = {name: 'Data not available', loaded: false};

    this.store.setForecast(forecast);
  }

  /**
   * Processes the call to the weather api.
   * @param city 
   * @param date
   */
  processWeatherCall(city: City, date: string) {
    const target = moment(date, 'YYYY-MM-DD');
    const isTargetSameOrAfterToday = target.isSameOrAfter(moment(), 'day');
    const endPoint = isTargetSameOrAfterToday ? '' : '/timemachine';
    const parameters = this.getHttpParams(city, target, 
        isTargetSameOrAfterToday);
    const finalUrl = [this.baseUrl, endPoint].join('');
    
    this.makeCall(finalUrl, parameters, isTargetSameOrAfterToday, date);
  }

  /**
   * Makes the http get call to the given url, with the given query params.
   *    Calls the process response function if Ok status is got from call.
   * @param url 
   * @param parameters 
   * @param isTargetSameOrAfterToday 
   * @param date 
   */
  makeCall(url: string, parameters: any, isTargetSameOrAfterToday, 
      date: string) {
    this.httpService.get<any>(url, {params: parameters})
        .pipe(tap((data) => this.processResponse(data, isTargetSameOrAfterToday, 
            date))).toPromise();
  }

  /**
   * Gets the forecast from the response and save it in the store.
   * @param data 
   * @param isTargetSameOrAfterToday 
   * @param date 
   */
  processResponse(data: any, isTargetSameOrAfterToday: boolean, date: string) {
    const forecast = this.getForecast(data, isTargetSameOrAfterToday, date);

    this.store.setForecast(forecast);
  }

  /**
   * Returns the forecast for the given date.
   * @param data 
   * @param isTargetSameOrAfterToday 
   * @param date 
   */
  getForecast(data: any, isTargetSameOrAfterToday: boolean, date: string) {
    const forecast = {name: data.current.weather[0].main, loaded: true};

    forecast.name = isTargetSameOrAfterToday ? 
        this.getWeatherSameAfter(data, date) : forecast.name;
    
    return forecast as Forecast;
  }

  /**
   * Retusn the corresponding forecast based in the difference in days between
   *    the target date and today's date. If target date is from future, returns
   *    the value from the daily info.
   * @param data 
   * @param date 
   */
  getWeatherSameAfter(data: any, date: string) {
    const index = (moment(date).diff(
        moment().format('YYYY-MM-DD'), 'days') % 9) - 1;

    return index > -1 ? data.daily[index].weather[0].main : 
        data.current.weather[0].main;
  }

  /**
   * Returns the query params needed to do the http request.
   * @param city 
   * @param target 
   * @param isTargetSameOrAfterToday 
   */
  getHttpParams(city: City, target: moment.Moment, 
      isTargetSameOrAfterToday: boolean) {
    const parameters: any = {lat: city.lat, lon: city.lng, appid: this.key};
    
    if (!isTargetSameOrAfterToday) parameters.dt = moment(target).format('X');

    return parameters;
  }

  /**
   * Check if the given date is between the allowed boundaries to make the http
   *    call.
   * @param date 
   */
  isDateBetweenAllowedRange(date: string) {
    const lowerBound = moment().subtract(6, 'day');
    const upperBound = moment().add(8, 'day');
    const target = moment(date, 'YYYY-MM-DD');
    
    return target.isBetween(lowerBound, upperBound);
  }
}