import { Component, OnInit, OnDestroy } from '@angular/core';
import { DayViewerService } from '../../services/day-viewer.service';
import { Reminder } from '../../model/reminder.model';
import * as moment from 'moment';

@Component({
  selector: 'app-day-viewer',
  templateUrl: './day-viewer.component.html',
  styleUrls: ['./day-viewer.component.scss']
})
export class DayViewerComponent implements OnInit, OnDestroy {

  public reminders: Array<Reminder>;

  public date: string;

  constructor(
      private dayViewerService: DayViewerService,
  ) { }

  /**
   * Subscribes the componet's listeners.
   */
  ngOnInit(): void {
    this.dayViewerService.subscribeToDateChange(this.onDateChange.bind(this));
  }

  ngOnDestroy(): void {
    this.dayViewerService.unsuscribeToDateChange();
  }

  /**
   * Closes the day viewer.
   */
  close() {
    this.dayViewerService.closeViewer();
  }

  /**
   * Handles the on data change, loads the reminders for that date.
   * @param date 
   */
  onDateChange(date) {
    this.date = moment(date).format('MMMM DD, YYYY');
    this.reminders = this.dayViewerService.getDayReminders(date);
  }

  /**
   * Opens the reminder editor with the given reminder loaded.
   * @param reminderId 
   */
  openReminder(reminder=null) {
    this.dayViewerService.openReminderEditor(reminder);
  }

  /**
   * Removes all the reminders of the current date.
   */
  deleteAll() {
    const ids = this.reminders.map((reminder) => reminder.id);
    
    this.dayViewerService.deleteAll(ids);
  }
}
