import { Injectable } from '@angular/core';
import { ReminderStore, ReminderState } from './reminder.store';
import { QueryEntity } from '@datorama/akita';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root'
})
export class ReminderQuery extends QueryEntity<ReminderState> {

  getSelectedReminder$ = this.select(state => state.ui.selected);

  getCurrentMonth$ = this.select(state => state.ui.current_month);

  getCurrentDate$ = this.select(state => state.ui.current_date);

  getRemindersInMonth$ = this.select(state => state.ui.reminders_month);

  getDayViewerOpened$ = this.select(state => state.ui.day_viewer_opened);

  getReminderEditorOpened$ = this.select(state => state.ui.reminder_editor_opened);

  getForecast$ = this.select(state => state.ui.forecast);

  constructor(protected store: ReminderStore) {
    super(store);
  }

  /**
   * Gets the current selected date.
   */
  getCurrentDate() {
    return this.getValue().ui.current_date;
  }

  /**
   * Gets the current selected month.
   */
  getCurrentMonth() {
    return this.getValue().ui.current_month;
  }

  /**
   * Returns the reminders associated to the given day.
   * @param day 
   */
  getRemindersInDay(day) {
    return this.getValue().ui.reminders_month[day] || [];
  }
}
