import { Injectable } from "@angular/core";
import { ReminderQuery } from "../store/reminder.query";
import { ReminderStore } from "../store/reminder.store";
import * as moment from 'moment';

const WeekDays = {
  FIRST_DAY: 0,
  LAST_DAY: 6,
};

@Injectable()
export class CalendarService {
  
  constructor(
    private reminderQuery: ReminderQuery,
    private store: ReminderStore
  ) {}

  /**
   * Subscribes the given callback to the current month change.
   * @param callback 
   */
  onCurrentMonthChange(callback) {
    this.reminderQuery.getCurrentMonth$.subscribe(callback);
  }

  /**
   * Subscribes the given callback to the reminders in month change.
   * @param callback 
   */
  onRemindersInMonthChange(callback) {
    this.reminderQuery.getRemindersInMonth$.subscribe(callback);
  }

  /**
   * Changes the value of the current_month adding the given unit.
   * @param unit 
   */
  moveCurrentMonth(unit) {
    const currentMonth = this.reminderQuery.getCurrentMonth();
    const newMonth = moment(currentMonth).add(unit, 'month').format('YYYY-MM');
    
    this.store.setCurrentMonth(newMonth);
  }

  /**
   * Changes the current date with the newDate given.
   * @param newDate 
   */
  changeCurrentDate(newDate) {
    this.store.setCurrentDate(newDate);
    this.store.setDayViewerOpened(true);
  }

  /**
   * Triggers the process to update the list of reminders associated with the 
   *    current month.
   */
  updateRemindersForCurrentMonth() {
    this.store.updateRemindersForCurrentMonth();
  }

  /**
   * Returns an array of days in the month completing it with days of previous
   *    and next month to complete first and last week.
   * @param month 
   */
  getNormalizedDaysInMonth(month) {
    const daysInMonth = moment(month).daysInMonth();
    const lastMonthWeekDay = moment(month).endOf('month').day();
    const firstMonthWeekDay = moment(month).startOf('month').day();

    const daysToGetPreviousMonthLastDay = firstMonthWeekDay - WeekDays.FIRST_DAY;
    const daysToCompleteMonthLastWeek = WeekDays.LAST_DAY - lastMonthWeekDay;
    const totalDays = daysInMonth 
        + daysToGetPreviousMonthLastDay 
        + daysToCompleteMonthLastWeek;
    
    const initDate = moment(month)
        .startOf('month')
        .subtract(daysToGetPreviousMonthLastDay, 'day')
        .format('YYYY-MM-DD');
    
    return this.generateDays(0, totalDays, initDate);
  }

  generateDays(init, end, initDate) {
    const days = [];

    while (init < end) {
      const day = moment(initDate).add(init, 'day');
      days.push(day);
      init++;
    }

    return days;
  }
}