import { Component, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { Reminder, City, Forecast } from '../../model/reminder.model';
import { ReminderService } from '../../services/reminder.service';
import { FormControl } from '@angular/forms';
import * as moment from 'moment';

@Component({
  selector: 'app-reminder-editor',
  templateUrl: './reminder-editor.component.html',
  styleUrls: ['./reminder-editor.component.scss']
})
export class ReminderEditorComponent implements OnInit, OnDestroy {

  /**
   * Model associated with the form.
   */
  public reminder: Partial<Reminder>;

  /**
   * List of cities.
   */
  public cities: Array<any>;

  /**
   * Indicates if the forms has an attempt of been submitted.
   */
  public submitted: boolean = false;

  /**
   * Indicates if the selected city is valid.
   */
  private isValidCity: boolean = false;

  /**
   * Indicates if the selected date is valid.
   */
  private isValidDate: boolean = true;

  constructor(
      private reminderService: ReminderService,
      private ref: ChangeDetectorRef,
  ) { }

  /**
   * Initialzes component's subscriptions.
   */
  ngOnInit(): void {
    this.reminderService.onSelectedChange(this.onReminderChange.bind(this));
    this.reminderService.onForecastChange(this.onForecastChange.bind(this));
  }

  /**
   * Unsubscribe from the observers.
   */
  ngOnDestroy(): void {
    this.reminderService.unsubscribeObservers();
  }

  /**
   * Updates the forecast of the current reminder with the one given.
   * @param forecast 
   */
  onForecastChange(forecast) {
    this.reminder.forecast.name = forecast?.name || '';
    this.reminder.forecast.loaded = forecast?.loaded || true;
    
    this.ref.detectChanges();
  }

  /**
   * Handles the on submit of the reminder editor form.
   * @param reminderForm
   */
  onSubmit(reminderForm) {
    this.submitted = true;

    if (reminderForm.valid) {
      this.reminder.date = this.getNormalizedDate(this.reminder.date);
      this.reminderService.saveReminder(this.reminder);
      this.resetForm(reminderForm);
    }
  }

  /**
   * Returns a normalized date based on the one given. If not valid, today's 
   *    date is returned.
   * @param date 
   */
  getNormalizedDate(date: string) {
    const normalizedDate = moment(date);

    return normalizedDate.isValid() ? normalizedDate.format('YYYY-MM-DD') : 
        moment().format('YYYY-MM-DD');
  }

  /**
   * Handles the change of the selected reminder to populate or clean the form
   *    values with the given reminder.
   * @param reminder
   */
  onReminderChange(reminder: Partial<Reminder>) {
    this.reminder = {
      color: '#fff',
      text_color: '#000',
      date: this.reminderService.getCurrentDate(),
      ...reminder
    };

    this.reminder.city = {name: '', lat: 0, lng: 0};
    this.reminder.forecast = {name: '', loaded: false, ...reminder.forecast};
    this.onCityChange(reminder.city);
  }

  /**
   * Resets to the original form state.
   * @param reminderForm 
   */
  resetForm(reminderForm) {
    this.submitted = false;
  }

  /**
   * Closes the reminder editor.
   */
  close() {
    this.reminderService.closeEditor();
  }

  /**
   * Deletes the reminder.
   */
  deleteReminder() {
    this.reminderService.deleteReminder(this.reminder.id);
  }

  /**
   * Triggers the weather change when the city is changed.
   * @param city 
   */
  onCityChange(city: City) {
    this.isValidCity = Boolean(city);

    this.reminder.city.name = city?.name || '';
    this.reminder.city.lat = city?.lat || 0;
    this.reminder.city.lng = city?.lng || 0;
     
    this.updateWeather();
  }

  /**
   * Triggers the weather change when the date is changed.
   * @param dateField
   */
  onDateChange(dateField: FormControl) {
    this.isValidDate = dateField.valid;
    this.reminder.date = dateField.value;

    this.updateWeather();
  }

  /**
   * Triggers the process to get a new weather if the city and the date are 
   *    valid.
   */
  private updateWeather() {
    const isValidData = this.isValidCity && this.isValidDate;
    
    isValidData ? 
        this.reminderService.getWeather(this.reminder.city, this.reminder.date) :
        this.onForecastChange(null);
  }
}
