import { Component, OnInit } from '@angular/core';
import { CalendarService } from '../../services/calendar.service';
import * as moment from 'moment';

const DATE_FORMAT = 'YYYY-MM-DD';

const MONTH_FORMAT = 'YYYY-MM';

const LABEL_MONTH = 'MMMM YYYY';

@Component({
  selector: 'app-calendar',
  templateUrl: './calendar.component.html',
  styleUrls: ['./calendar.component.scss']
})
export class CalendarComponent implements OnInit {

  /**
   * Normalized list of days associated to the current month.
   */
  public days: Array<any>;

  /**
   * Current month displayed in the calendar.S
   */
  public currentMonth: string;
  
  /**
   * Current month name.
   */
  public monthName: string;

  /**
   * List of headers.
   */
  public headers: Array<string> = [
    'Sunday',
    'Monday',
    'Tuesday',
    'Wednesday',
    'Thursday',
    'Friday',
    'Saturday',
  ];

  /**
   * Days in week.
   */
  public daysInWeek: number = 7;

  constructor(
    private calendarService: CalendarService
  ) { }

  /**
   * Initialzes component's subscriptions.
   */
  ngOnInit(): void {
    this.calendarService.onCurrentMonthChange(this.onMonthChange.bind(this));
    this.calendarService.onRemindersInMonthChange(
        this.onRemindersChange.bind(this))
  }

  /**
   * Handles the current month change to 
   * @param currentMonth 
   */
  onMonthChange(currentMonth) {
    this.currentMonth = currentMonth;
    this.monthName = moment(this.currentMonth).format(LABEL_MONTH);
    this.updateDays(this.calendarService.getNormalizedDaysInMonth(currentMonth));
    this.calendarService.updateRemindersForCurrentMonth();
  }

  updateDays(daysInMonth) {
    this.days = daysInMonth.map((day) => {
      return {
        date: day,
        reminders: 0,
      }
    })
  }

  /**
   * Updates the current month adding +/- unit.
   * @param unit 
   */
  moveMonth(unit) {
    this.calendarService.moveCurrentMonth(unit);
  }

  /**
   * Changes the current date.
   * @param date 
   */
  selectDate(date) {
    const dateMonth = moment(date).format(MONTH_FORMAT);

    if (dateMonth === this.currentMonth) {
      this.calendarService.changeCurrentDate(date.format(DATE_FORMAT));
    }
  }

  /**
   * Associates the given reminders to their corresponding dates in the 
   *    calendar.
   * @param reminders 
   */
  onRemindersChange(reminders) {
    this.days = this.days.map((day) => {
      const dateString = day.date.format(DATE_FORMAT);
      const dateReminders = reminders[dateString] || [];
      
      return {
        date: day.date,
        reminders: dateReminders.length,
      }
    });
  }
}
