import { Injectable } from "@angular/core";
import { Reminder, City } from "../model/reminder.model";
import { ReminderQuery } from "../store/reminder.query";
import { ReminderStore } from "../store/reminder.store";
import * as uuid from 'uuid';
import { Subscription } from "rxjs";
import { WeatherService } from "./weather.service";

@Injectable()
export class ReminderService {

  private subscription: Subscription;

  private forecastSubscription: Subscription;

  /**
   * Service constructor
   * @param store Reminders store.
   */
  constructor(
      private reminderQuery: ReminderQuery,
      private store: ReminderStore,
      private weatherService: WeatherService,
  ) {}

  /**
   * Subscribes the given callback to reminder selected changes.
   * @param callback 
   */
  onSelectedChange(callback) {
    this.subscription = 
        this.reminderQuery.getSelectedReminder$.subscribe(callback);
  }

  /**
   * Subscribes the given callback to forecast changes.
   * @param callback 
   */
  onForecastChange(callback) {
    this.forecastSubscription = 
        this.reminderQuery.getForecast$.subscribe(callback);
  }

  /**
   * Unsubscribes from the observers.
   */
  unsubscribeObservers() {
    this.subscription.unsubscribe();
    this.forecastSubscription.unsubscribe();
  }

  /**
   * Saves the give reminder in the store. It determines if should update an 
   *    existing reminder or create a new one. Clears the selected reminder.
   * @param reminder
   */
  saveReminder(reminderToSave: Partial<Reminder>) {
    reminderToSave.id ? this.updateReminder(reminderToSave as Reminder) : 
        this.insertReminder(reminderToSave);
    this.store.updateRemindersForCurrentMonth();
    this.store.clearSelected();
    this.closeEditor();
  }

  /**
   * Updates the values of the given reminder in the store.
   * @param reminder
   */
  updateReminder(reminder: Reminder) {
    this.store.update(reminder.id, reminder);
  }

  /**
   * Inserts a new reminder in the store. It assigns a new ID and the selected
   *    date to the reminder.
   * @param reminder
   */
  insertReminder(reminder: Partial<Reminder>) {
    reminder.id = uuid.v4();

    this.store.add(reminder as Reminder);
  }

  /**
   * Returns the current date.
   */
  getCurrentDate() {
    return this.reminderQuery.getCurrentDate();
  }

  /**
   * Closes the reminder editor.
   */
  closeEditor() {
    this.store.setReminderEditorOpened(false);
  }
  
  /**
   * Deletes the reminder with the given id.
   * @param id
   */
  deleteReminder(id: string) {
    this.store.remove(id);
    this.store.updateRemindersForCurrentMonth();
    this.closeEditor();
  }

  /**
   * Calls the weather service to update the forecast.
   * @param city 
   * @param date 
   */
  getWeather(city: City, date: string) {
    this.weatherService.getWeatherForCity(city, date);
  }
}