import { Injectable } from "@angular/core";
import { ReminderStore } from "../store/reminder.store";
import { ReminderQuery } from "../store/reminder.query";
import { Subscription } from "rxjs";
import * as moment from 'moment';

@Injectable()
export class DayViewerService {

  private subscription: Subscription;

  constructor(
      private store: ReminderStore,
      private reminderQuery: ReminderQuery
  ) { }
  
  /**
   * Sets the value of day viewer opened to false.
   */
  closeViewer() {
    this.store.setDayViewerOpened(false);
  }

  /**
   * Subscribes the given callback to the current date change.
   * @param callback 
   */
  subscribeToDateChange(callback) {
    this.subscription = this.reminderQuery.getCurrentDate$.subscribe(callback);
  }

  /**
   * Unsubscribes from the get current date observable.
   */
  unsuscribeToDateChange() {
    this.subscription.unsubscribe();
  }

  /**
   * Returns the reminders of the given day.
   * @param day 
   */
  getDayReminders(day) {
    const reminders = this.reminderQuery.getRemindersInDay(day);

    reminders.sort((firstReminder, secondReminder) => (
      this.isHourLessThanOther(firstReminder.time, secondReminder.time, day)
    ));

    return reminders
  }

  /**
   * Returns true if the firstHour is less than the secondHour
   * @param firstHour 
   * @param secondHour 
   * @param day 
   */
  isHourLessThanOther(firstHour: string, secondHour: string, day: string) {
    const momentFirstHour = moment(`${day} ${firstHour}`);
    const momentSecondHour = moment(`${day} ${secondHour}`);

    return momentFirstHour.isBefore(momentSecondHour) ? -1 : 1;
  }

  /**
   * Open the reminder editor with the given reminder selected. If no reminder
   *    is given, the editor opens to create a new reminder.
   * @param reminderId 
   */
  openReminderEditor(reminder) {
    const selectedReminder = reminder || {};
    
    this.store.setSelected(selectedReminder);
    this.store.setDayViewerOpened(false);
    this.store.setReminderEditorOpened(true);
  }

  /**
   * Deletes all the reminders with the given ids.
   * @param ids 
   */
  deleteAll(ids: Array<string>) {
    this.store.remove(ids);
    this.store.updateRemindersForCurrentMonth();
    this.closeViewer();
  }
}