import { Component, OnInit } from '@angular/core';
import { ReminderQuery } from '../../store/reminder.query';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-tool-wrapper',
  templateUrl: './tool-wrapper.component.html',
  styleUrls: ['./tool-wrapper.component.scss']
})
export class ToolWrapperComponent implements OnInit {

  public isDayViewerOpened: Observable<boolean>;

  public isReminderEditorOpened: Observable<boolean>;

  constructor(
      private reminderQuery: ReminderQuery,
  ) { }

  ngOnInit(): void {
    this.isDayViewerOpened = this.reminderQuery.getDayViewerOpened$;
    this.isReminderEditorOpened = this.reminderQuery.getReminderEditorOpened$;
  }
}
