import { } from 'googlemaps';
import { Directive, ElementRef, AfterViewInit, OnDestroy, Output, EventEmitter, HostListener, platformCore } from '@angular/core';
import { NgModel } from '@angular/forms';

const AUTOCOMPLETE_CHANGED_EVENT = 'place_changed';

@Directive({
  selector: '[appCitySelector]'
})
export class CitySelectorDirective implements AfterViewInit, OnDestroy {

  @Output() cityValueChange: EventEmitter<any> = new EventEmitter();

  /**
   * Google autocomplete.
   */
  private autocomplete: google.maps.places.Autocomplete;

  /**
   * Listener associated with city change.
   */
  private listener: google.maps.MapsEventListener;

  constructor(
    private model: NgModel,
    private elementRef: ElementRef,
  ) { }

  /**
   * Initialized the autocomplete.
   */
  ngAfterViewInit() {
    this.initAutocomplete();
  }

  /**
   * Removes the city change listener.
   */
  ngOnDestroy() {
    google.maps.event.removeListener(this.listener);
  }

  /**
   * Cleans the input.
   */
  @HostListener('change')
  cleanCity() {
    this.cityValueChange.emit(null);
  }

  /**
   * Creates a new instance of the autocomplete. Attaches the city change event.
   */
  private initAutocomplete() {
    this.autocomplete = new google.maps.places.Autocomplete(
        this.elementRef.nativeElement, {types: ['(cities)']});

    this.listener = google.maps.event.addListener(this.autocomplete,
        AUTOCOMPLETE_CHANGED_EVENT, this.onAutocompleteChanged.bind(this));
  }

  /**
   * Handles the autcomplete changes event.
   */
  private onAutocompleteChanged() {
    const place = this.autocomplete.getPlace();
    const isValidValue = place.geometry;

    isValidValue ? this.updateCity(place) : this.cleanCity();
  }

  /**
   * Emits the city changed value.
   * @param city 
   */
  private updateCity(city: google.maps.places.PlaceResult) {
    const cityName = city.formatted_address;

    this.cityValueChange.emit({
      name: cityName,
      lat: city.geometry.location.lat(),
      lng: city.geometry.location.lng(),
    });
  }
}
